'use client';

import { useFormState, useFormStatus } from 'react-dom';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "./ui/card";
import { Label } from "./ui/label";
import { Input } from "./ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "./ui/select";
import { Button } from "./ui/button";
import { updateUserAction } from '@/app/actions';
import type { User } from '@/lib/types';
import { useEffect } from 'react';
import { useToast } from './ui/use-toast';
import { Loader2 } from 'lucide-react';

const initialState = {
    message: '',
    status: '',
};

function SubmitButton() {
    const { pending } = useFormStatus();
    return (
        <Button type="submit" disabled={pending}>
            {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
            Save Changes
        </Button>
    );
}

function RequiredLabel({ htmlFor, children }: { htmlFor: string, children: React.ReactNode }) {
    return (
        <Label htmlFor={htmlFor}>
            {children} <span className="text-destructive">*</span>
        </Label>
    );
}

export function ProfileForm({ user }: { user: User }) {
    const [state, formAction] = useFormState(updateUserAction, initialState);
    const { toast } = useToast();

    useEffect(() => {
        if (state.message) {
            toast({
                title: state.status === 'success' ? 'Success' : 'Error',
                description: state.message,
                variant: state.status === 'success' ? 'default' : 'destructive',
            });
        }
    }, [state, toast]);

    return (
        <form action={formAction}>
          <input type="hidden" name="id" value={user.id} />
          <Card>
            <CardHeader>
                <CardTitle>Personal Information</CardTitle>
                <CardDescription>Update your personal and contact details.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
                 <div className="space-y-2">
                    <RequiredLabel htmlFor="name">Full Name</RequiredLabel>
                    <Input id="name" name="name" defaultValue={user.userProfile?.name} required />
                </div>
                <div className="space-y-2">
                    <RequiredLabel htmlFor="email">Email</RequiredLabel>
                    <Input id="email" name="email" defaultValue={user.contact?.email} readOnly className="bg-muted cursor-not-allowed opacity-50" />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                      <Label htmlFor="phone">Phone Number</Label>
                      <Input id="phone" name="phone" type="tel" defaultValue={user.contact?.phone || ''} />
                  </div>
                  <div className="space-y-2">
                      <Label htmlFor="whatsapp">WhatsApp Number</Label>
                      <Input id="whatsapp" name="whatsapp" type="tel" defaultValue={user.contact?.whatsapp || ''} />
                  </div>
                </div>
            </CardContent>
          </Card>

          <Card className="mt-6">
            <CardHeader>
              <CardTitle>Location</CardTitle>
              <CardDescription>Update your location details to see relevant group orders.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div className="space-y-2">
                        <RequiredLabel htmlFor="city">City</RequiredLabel>
                        <Input id="city" name="city" defaultValue={user.location?.city || ''} required />
                    </div>
                    <div className="space-y-2">
                        <RequiredLabel htmlFor="area">Area/Locality</RequiredLabel>
                        <Input id="area" name="area" defaultValue={user.location?.area || ''} required />
                    </div>
                </div>
                <div className="space-y-2">
                    <Label htmlFor="pinCode">Pin Code</Label>
                    <Input id="pinCode" name="pinCode" defaultValue={user.location?.pinCode || ''} />
                </div>
            </CardContent>
          </Card>
          
          <Card className="mt-6">
            <CardHeader>
              <CardTitle>Institution</CardTitle>
              <CardDescription>If you are a student, please provide your institution details.</CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
                <div className="space-y-2">
                    <Label htmlFor="institutionType">Institution Type</Label>
                    <Select name="institutionType" defaultValue={user.institution?.institutionType || ''}>
                        <SelectTrigger>
                            <SelectValue placeholder="Select type" />
                        </SelectTrigger>
                        <SelectContent>
                            <SelectItem value="College/University">College/University</SelectItem>
                            <SelectItem value="School">School</SelectItem>
                            <SelectItem value="Office">Office</SelectItem>
                        </SelectContent>
                    </Select>
                </div>
                <div className="space-y-2">
                    <Label htmlFor="institutionName">Institution Name</Label>
                    <Input id="institutionName" name="institutionName" defaultValue={user.institution?.institutionName || ''} />
                </div>
                 <div className="space-y-2">
                    <Label htmlFor="hostelOrPG">Hostel/PG Name (if applicable)</Label>
                    <Input id="hostelOrPG" name="hostelOrPG" defaultValue={user.institution?.hostelOrPG || ''} />
                </div>
            </CardContent>
            <CardFooter className="justify-end">
              <SubmitButton />
            </CardFooter>
          </Card>
        </form>
    );
}
